import * as React from 'react';
import classNames from 'classnames';
import { graphql } from 'gatsby';
import { Container, Row, Column } from '../components/grid/Grid';
import GatsbyImage from '../components/gatsbyImage/GatsbyImage';
import BlockContent from '../components/blockContent/BlockContent';
import Layout from '../components/layout/Layout';
import PageTitle from '../components/pageTitle/PageTitle';
import Section from '../components/section/Section';
import blockContentSerializerBrown from '../components/blockContent/blockContentSerializerBrown';
import BookMenu from '../components/bookMenu/BookMenu';
import styles from './PageTemplate.module.css';

export const query = graphql`
  query PageTemplateQuery($id: String!) {
    page: sanityPage(id: { eq: $id }) {
      title
      image: _rawImage(resolveReferences: { maxDepth: 5 })
      body: _rawBody(resolveReferences: { maxDepth: 5 })
    }
  }
`;

type Props = {
  data: {
    page: {
      body: any;
      image?: any;
      title: string;
    };
  };
} & React.HTMLAttributes<HTMLElement>;

const renderRow = (props: Props) => {
  if (props.data.page.image != null) {
    return (
      <Row>
        <Column xs={12} md={6}>
          <BlockContent blocks={props.data.page.body} serializers={blockContentSerializerBrown} />
        </Column>
        <Column xs={12} mdOffset={1} md={5}>
          <GatsbyImage asset={props.data.page.image.asset} className={styles.image} />
        </Column>
      </Row>
    );
  }
  return (
    <Row>
      <Column xs={12} sm={10} smOffset={1} lg={8} lgOffset={2}>
        <BlockContent blocks={props.data.page.body} serializers={blockContentSerializerBrown} />
      </Column>
    </Row>
  );
};

const PageTemplate: React.FunctionComponent<Props> = ({ className, ...props }) => {
  return (
    <Layout className={styles.layout}>
      <Section>
        <PageTitle theme="brown" title="" className={styles.pageTitle} />
        <Container>{renderRow(props)}</Container>
      </Section>
      <BookMenu />
    </Layout>
  );
};

export default PageTemplate;
